import { useEffect, useRef } from "react";
export function useMobxInitializer(MobxCreator, initialParams) {
  var renderStatusRef = useRef({
    isFirstRender: true
  });
  useEffect(function () {
    return function () {// MobxCreator.destroy()
    };
  }, []);

  if (renderStatusRef.current.isFirstRender) {
    MobxCreator.destroy();
    renderStatusRef.current.isFirstRender = false;
  }

  var instance = MobxCreator.getInstance(initialParams);
  return instance;
}